@font-face {
  font-family: "29LT Bukra";
  src: url("../src/fonts/29ltbukra.ttf");
  src: local("29LT Bukra Light"), local("29LTBukra-Light"), format("ttf");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: "BukraAlt_Bold";
  src: url("../src/fonts/BukraAlt_Bold.ttf");
  src: local("BukraAlt_Bold"), local("BukraAlt_Bold"), format("woff2");
  font-weight: 900;
  font-style: normal;
}

* {
  font-family: "29LT Bukra";
}

html,
body {
  min-height: 100%;
}

body {
  direction: rtl;
  background: #f9f9f9;
}

a {
  color: #000;
  transition: transition-duration, 0.5s ease;
  text-decoration: none !important;
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.App {
  min-height: 100%;
}

.form-control {
  font-size: 12px;
}

.form-control:focus {
  border-color: #00c269;

  box-shadow: none;
}
